<template>
  <div class="container">
    <loading :active="isLoading"></loading>
    <div class="text-end mt-3">
      <button class="btn btn-primary" type="button" @click="openCouponModal(true)">新增優惠卷</button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th>名稱</th>
          <th>優惠碼</th>
          <th>折扣百分比</th>
          <th>到期日</th>
          <th>是否啟用</th>
          <th>編輯</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, key) in coupons" :key="key">
          <td>{{ item.title }}</td>
          <td>{{ item.code }}</td>
          <td>{{ item.percent }}%</td>
          <td>{{ $filters.date(item.due_date) }}</td>
          <td>
            <span v-if="item.is_enabled === 1" class="text-success">啟用</span>
            <span v-else class="text-muted">未起用</span>
          </td>
          <td>
            <div class="btn-group">
              <button class="btn btn-outline-primary btn-sm" @click="openCouponModal(false, item)">編輯</button>
              <button class="btn btn-outline-danger btn-sm" @click="openDelCouponModal(item)">刪除</button>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
    <CouponModal ref="couponModal" :coupon="tempCoupon" @update-coupon="updateCoupon"></CouponModal>
    <DelModal :item="tempCoupon" ref="delModal" @del-item="delCoupon" />
  </div>
</template>

<script>
import CouponModal from '@/components/Dashboard/CouponModal.vue'
import DelModal from '@/components/Dashboard/DelModal.vue'
export default {
  components: { CouponModal, DelModal },
  data () {
    return {
      coupons: [],
      tempCoupon: {
        title: '',
        is_enabled: 0,
        percent: 100,
        code: ''
      },
      pagination: {},

      isNew: false,
      isLoading: false
    }
  },
  inject: ['emitter'],
  methods: {
    openCouponModal (isNew, item) {
      this.isNew = isNew
      if (this.isNew) {
        this.tempCoupon = {
          due_date: new Date().getTime() / 1000
        }
      } else {
        this.tempCoupon = { ...item }
      }
      this.$refs.couponModal.showModal()
    },
    openDelCouponModal (item) {
      this.tempCoupon = { ...item }
      const delComponent = this.$refs.delModal
      delComponent.showModal()
    },

    getCoupons (page = 1) {
      this.isLoading = true
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/coupons/?page=${page}`
      this.$http.get(api)
        .then((res) => {
          this.isLoading = false
          if (res.data.success) {
            // console.log(res.data)
            this.coupons = res.data.coupons
            this.pagination = res.data.pagination
          }
        })
    },
    updateCoupon (tempCoupon) {
      if (this.isNew) {
        const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/coupon`
        this.$http.post(url, { data: tempCoupon }).then((response) => {
          // console.log(response, tempCoupon)
          this.$httpMessageState(response, '新增優惠券')
          this.getCoupons()
          this.$refs.couponModal.hideModal()
        })
      } else {
        const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/coupon/${this.tempCoupon.id}`
        this.$http.put(url, { data: this.tempCoupon }).then((response) => {
          // console.log(response)
          this.$httpMessageState(response, '更新優惠券')
          this.getCoupons()
          this.$refs.couponModal.hideModal()
        })
      }
    },
    delCoupon () {
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/coupon/${this.tempCoupon.id}`
      this.isLoading = true
      this.$http.delete(url).then((response) => {
        // console.log(response, this.tempCoupon)
        this.$httpMessageState(response, '刪除優惠券')
        const delComponent = this.$refs.delModal
        delComponent.hideModal()
        this.getCoupons()
      })
    }
    // updateProduct (item) {
    //   this.isLoading = true
    //   this.tempProduct = item
    //   // 新增狀態
    //   let api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/product`
    //   let httpMethod = 'post'
    //   // 編輯狀態
    //   if (!this.isNew) {
    //     api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/product/${item.id}`
    //     httpMethod = 'put'
    //   }
    //   const productComponent = this.$refs.productModal
    //   this.$http[httpMethod](api, { data: this.tempProduct })
    //     .then((res) => {
    //       productComponent.hideModal()
    //       if (res.data.success) {
    //         this.getProducts()
    //         console.log('更新成功')
    //         this.emitter.emit('push-message', {
    //           style: 'success',
    //           title: '更新成功'
    //         })
    //       } else {
    //         this.emitter.emit('push-message', {
    //           style: 'danger',
    //           title: '更新失敗',
    //           content: res.data.message.join('、')
    //         })
    //       }
    //       this.isLoading = false
    //     })
    // },
    // delProduct () {
    //   const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/product/${this.tempProduct.id}`
    //   this.isLoading = true
    //   this.$http.delete(api)
    //     .then(res => {
    //       this.isLoading = false
    //       const delComponent = this.$refs.delModal
    //       delComponent.hideModal()
    //       this.getProducts()
    //     })
    // }
  },
  created () {
    this.getCoupons()
  }
}
</script>
